@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
:root {
  --accent-font: "Oswald", "Impact", sans-serif;
  --main-font: "Arial", "Helvetica", sans-serif;
}

@custom-media --narrow-vp (width <= 767.9px);
@custom-media --not-narrow-vp (width >= 768px);
@custom-media --wide-vp (width >= 1280px);
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.title-line {
  --air-gap: 23px;
  --line-color: #68b738;
  position: relative;
}
.title-line::before {
  content: "";
  position: absolute;
  background-color: var(--line-color);
  height: 2px;
  width: calc(100vw - 100% - var(--air-gap));
  right: calc(100% + var(--air-gap));
  box-shadow: calc(100vw + var(--air-gap)) 0 0 0 var(--line-color);
}

.page {
  height: 100%;
}

.page__body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0;
  min-height: 100%;
  font-family: var(--accent-font);
  font-weight: 400;
  overflow-x: hidden;
}

@media (--wide-vp) {
  .page__body--index .main-navigation__link {
    color: #ffffff;
  }
  .page__body--index .main-navigation__link[aria-current=page] {
    border-bottom: 2px solid #ffffff;
  }
}

.page__body--index .cooperation {
  background-color: #ffffff;
}
@media (--wide-vp) {
  .page__body--index .cooperation {
    background-color: transparent;
  }
}

.page__centerer {
  margin: 0 20px;
}
@media (--not-narrow-vp) {
  .page__centerer {
    margin: 0 30px;
  }
}
@media (--wide-vp) {
  .page__centerer {
    width: 1220px;
    margin: 0 auto;
  }
}

.page__aside {
  display: grid;
}

.button {
  display: block;
  padding: 10px 0;
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #68b738;
  border: none;
  box-sizing: border-box;
}
.button--size_wide {
  width: 100%;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--show-more {
  color: #444444;
  background-color: #f2f2f2;
}
.button--show-more:hover {
  background-color: #ebebeb;
}
.button--show-more:active {
  color: rgba(68, 68, 68, 0.3);
}

.header {
  display: flex;
  flex-direction: column;
}
@media (--not-narrow-vp) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}

.header__logo {
  margin: 11px 0 11px;
  display: grid;
  grid-template-columns: 1fr 0fr 1fr;
  align-items: center;
}
@media (--not-narrow-vp) {
  .header__logo {
    display: flex;
    flex-wrap: wrap;
    width: 210px;
    margin: 22px 0 0;
  }
}
@media (--wide-vp) {
  .header__logo {
    margin: 51px 0 0;
  }
}

.header__cat-logo {
  width: fit-content;
}

.header__logo-img {
  width: 34px;
  height: 38px;
}
.header__logo-img:hover {
  opacity: 0.8;
}
.header__logo-img:active {
  opacity: 0.6;
}
@media (--not-narrow-vp) {
  .header__logo-img {
    width: 60px;
    height: 50px;
    margin-right: 12px;
  }
}
@media (--wide-vp) {
  .header__logo-img {
    width: 70px;
    height: 60px;
  }
}

.header__logo-svg {
  width: 102px;
  height: 19px;
}
.header__logo-svg:hover {
  opacity: 0.8;
}
.header__logo-svg:active {
  opacity: 0.6;
}
@media (--not-narrow-vp) {
  .header__logo-svg {
    width: 102px;
    height: 62px;
    --fill: #444444;
  }
}
@media (--wide-vp) {
  .header__logo-svg {
    margin-top: 10px;
    width: 120px;
    height: 23px;
  }
}

