}

.gift__description {
  margin: 0 auto;
  padding-top: 187px;
  width: 161px;
  text-align: center;
  font-family: var(--main-font);
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
}
.gift__description::before {
  position: absolute;
  top: 60px;
  left: auto;
  width: 80px;
  height: 80px;
  background: url("../icons/sprite.svg#gift") no-repeat center;
