  }
}
@media (--wide-vp) {
  .catalog-page__title {
    padding: 72px 0 165px;
  }
}

.promo {
  display: flex;
  flex-direction: column;
  text-align: center;
  color: #ffffff;
}
@media (--not-narrow-vp) {
