}
@media (min-resolution: 2dppx) {
  .promo__wrapper::before {
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/cat-background-index@2x.jpg");
  }
}
@media (--not-narrow-vp) {
  .promo__wrapper::before {
    position: initial;
  }
}
@media (--wide-vp) {
  .promo__wrapper::before {
    position: absolute;
    height: 694px;
    left: 50%;
    background-position: 0% 0;
    background-size: cover;
  }
}

.promo__title {
  margin: 0 auto;
  padding: 27px 0 25px;
