  }
}
@media (--wide-vp) {
  .promo__img {
    margin: 0 0 -237px 0;
    top: -368px;
    right: -448px;
    width: 552px;
    height: 499px;
  }
}

.promo__button {
  text-align: center;
  font-size: 14px;
  line-height: 18px;
}
@media (--not-narrow-vp) {
  .promo__button {
