}

.selection__title {
  margin: 0 0 29px 69px;
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  text-transform: uppercase;
}
.selection__title::before {
  position: absolute;
  top: 14px;
  left: 11px;
  width: 72px;
  height: 72px;
  background-repeat: no-repeat;
  background-position: center;
  content: "";
}
.selection__title--slim::before {
  background-image: url("../icons/sprite.svg#cat-slim");
}
.selection__title--pro::before {
  background-image: url("../icons/sprite.svg#cat-pro");
}
@media (--not-narrow-vp) {
  .selection__title {
    margin: 0 0 26px;
    font-size: 36px;
    line-height: 36px;
  }
  .selection__title::before {
    top: 53px;
    left: auto;
    right: 22px;
    width: 280px;
    height: 280px;
  }
}
@media (--wide-vp) {
  .selection__title {
    margin: 0 0 73px 162px;
  }
  .selection__title::before {
    top: 27px;
    left: 32px;
    width: 140px;
    height: 140px;
  }
}

.selection__description {
  margin-bottom: 20px;
  padding-bottom: 20px;
  min-width: 243px;
  font-family: var(--main-font);
  font-size: 14px;
  line-height: 18px;
  text-align: left;
  color: #444444;
  border-bottom: 1px solid #d9d9d9;
}
@media (--not-narrow-vp) {
  .selection__description {
    margin: 0 0 51px;
    width: 340px;
    padding: 0;
    border: none;
    font-size: 16px;
    line-height: 24px;
  }
}
@media (--wide-vp) {
  .selection__description {
    margin: 0 0 27px 0;
    width: 462px;
  }
}

.selection__link {
  position: relative;
  display: block;
  margin-bottom: 3px;
  font-size: 16px;
  line-height: 16px;
  text-decoration: none;
  text-transform: uppercase;
  text-align: left;
  color: #000000;
  width: 131px;
}
.selection__link::after {
  position: absolute;
  width: 26px;
  height: 26px;
  top: -4px;
  right: 4px;
  background-image: url("../icons/sprite.svg#arrow");
  background-repeat: no-repeat;
  background-position: center;
  content: "";
}
.selection__link:hover::after {
  width: 36px;
  height: 36px;
  top: -9px;
  right: -6px;
  background-image: url("../icons/sprite.svg#skale-arrow");
}
.selection__link:active {
  opacity: 0.3;
}
@media (--not-narrow-vp) {
  .selection__link {
    font-size: 20px;
    line-height: 30px;
    border: none;
  }
  .selection__link::after {
    top: 1px;
    right: -26px;
  }
  .selection__link:hover::after {
    top: -4px;
    right: -36px;
  }
}

@media (--not-narrow-vp) {
  .main-navigation {
    margin-top: 42px;
  }
}
@media (--wide-vp) {
  .main-navigation {
    margin-top: 68px;
  }
}

.main-navigation__toggle {
  position: absolute;
  display: block;
  padding: 0;
  top: 20px;
  right: 20px;
  width: 24px;
  height: 19px;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
.main-navigation__toggle:hover {
  opacity: 0.8;
}
.main-navigation__toggle:active {
  opacity: 0.6;
}
@media (--not-narrow-vp) {
  .main-navigation__toggle {
    display: none;
  }
}

.main-navigation--close .main-navigation__toggle::before {
  content: "";
  position: absolute;
  top: 2px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.main-navigation--open .main-navigation__toggle::before, .main-navigation--open .main-navigation__toggle::after {
  content: "";
  position: absolute;
  top: 11px;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: rotate(45deg);
}
.main-navigation--open .main-navigation__toggle::after {
  transform: rotate(-45deg);
}

.main-navigation--close .main-navigation__list {
  display: none;
}
@media (--not-narrow-vp) {
  .main-navigation--close .main-navigation__list {
    display: flex;
  }
}

.main-navigation--jsnone .main-navigation__toggle {
  display: none;
}

.main-navigation--jsnone .main-navigation__list {
  position: static;
}
@media (--not-narrow-vp) {
  .main-navigation--jsnone .main-navigation__list {
    display: flex;
  }
}

.main-navigation__list {
  margin: 0;
  padding: 0;
  position: absolute;
  left: 0;
  right: 0;
  z-index: 2;
  background-color: #ffffff;
  list-style-type: none;
}
@media (--not-narrow-vp) {
  .main-navigation__list {
    width: 480px;
    justify-content: flex-end;
    flex-wrap: wrap;
    position: static;
    align-items: center;
    background-color: transparent;
  }
}

.main-navigation__item::before {
  position: absolute;
  height: 1px;
  right: 0;
  left: 0;
  background-color: #e6e6e6;
  content: "";
}
.main-navigation__item:last-child::after {
  position: absolute;
  height: 1px;
  right: 0;
  left: 0;
  background-color: #e6e6e6;
  content: "";
}
@media (--not-narrow-vp) {
  .main-navigation__item {
    margin-right: 36px;
  }
  .main-navigation__item:last-child {
    margin: 0;
  }
  .main-navigation__item::before {
    position: initial;
  }
  .main-navigation__item:last-child::after {
    position: initial;
  }
}
@media (--wide-vp) {
  .main-navigation__item {
    margin-right: 35px;
  }
  .main-navigation__item:last-child {
    margin: 0;
  }
}

.main-navigation__link {
  display: block;
  padding: 22px 0 24px;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
  text-decoration: none;
}
.main-navigation__link:hover {
  opacity: 0.6;
}
.main-navigation__link:active {
  opacity: 0.3;
}
@media (--not-narrow-vp) {
  .main-navigation__link {
    padding: 0 0 9px 0;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  .main-navigation__link[aria-current=page] {
    border-bottom: 2px solid #68b738;
  }
}
@media (--wide-vp) {
  .main-navigation__link {
    padding: 0 0 7px;
    font-size: 20px;
    line-height: 30px;
  }
}

.advantages__title {
  margin: 0;
  margin-bottom: 40px;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
}
@media (--not-narrow-vp) {
  .advantages__title {
    margin-bottom: 32px;
    font-size: 60px;
    line-height: 60px;
  }
}
@media (--wide-vp) {
  .advantages__title {
    margin-top: 51px;
    font-size: 60px;
    line-height: 60px;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  column-gap: 10px;
  row-gap: 26px;
  margin: 0 0 55px;
  padding: 0;
  list-style-type: none;
  counter-reset: number;
}
@media (--not-narrow-vp) {
  .advantages__list {
    margin: 0 0 46px;
    column-gap: 60px;
    row-gap: 37px;
  }
}
@media (--wide-vp) {
