    row-gap: 10px;
    margin: 0 0 69px;
  }
}

.advantages__item {
  position: relative;
  width: 274px;
}
.advantages__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  height: 60px;
}
.advantages__item--leaf::before {
  background-image: url("../icons/sprite.svg#leaf");
}
.advantages__item--cup::before {
  background-image: url("../icons/sprite.svg#cup");
}
.advantages__item--eat::before {
  background-image: url("../icons/sprite.svg#eat");
}
.advantages__item--time::before {
  background-image: url("../icons/sprite.svg#time");
}
@media (--not-narrow-vp) {
  .advantages__item {
    width: 245px;
  }
  .advantages__item::before {
    top: 40px;
    left: 0;
    width: 80px;
    height: 80px;
