.example__description {
  margin: 0;
  margin-bottom: 20px;
  font-family: var(--main-font);
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
@media (--not-narrow-vp) {
  .example__description {
    margin: 0 6px 54px 0;
    font-size: 16px;
